/*photos*/
.gallery_container{
    width:-webkit-fit-content;
    width:-moz-fit-content;
    width:fit-content;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 8px;
}

.gallery_image img{
    width:100%;
    height:100%;
    object-fit: cover;
    object-position:50% 50%;
}

