* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  /*background: url(leaf.svg);*/
  min-height: 100vh;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 16px;
  font-weight: 600;
  letter-spacing: .1em;
}

/* Navigation */
.navigation {
  position: fixed;
  top: 0;
  z-index: 2;
  width: 100%;
  background: rgba( 94, 164, 20, 0.45 );
  box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 );
  backdrop-filter: blur( 4px );
  -webkit-backdrop-filter: blur( 4px );
 /* display: flex;
  flex-direction: column;*/
}

.link {
  display: inline-block;
  text-decoration: none;
  text-transform: uppercase;
  color: rgb(63, 77, 64);
  padding: 15px;
}
.link.selected {
  background: rgba( 49, 78, 15, 0.45);
}

.link:hover {
  color: black;
  background: rgba( 49, 78, 15, 0.45);
}

